export const processJson = (response) => {
    //console.log(response);
    if (typeof response == "object") {
        var finalResult = [];
        var length = response.result["execution-results"].results.length;
        var resultArray = response.result["execution-results"].results;
        for (var i = 0; i < length; i++) {
            if (resultArray[i].key === 'questions') {
                var anotherArray = resultArray[i].value;
                for (var j = 0; j < anotherArray.length; j++) {
                    var oneMoreArray = Object.keys(anotherArray[j]);
                    for (var k = 0; k < oneMoreArray.length; k++) {
                        if (oneMoreArray[k] === 'com.fsi.creditcarddisputecase.AdditionalInformation') {
                            var tmp = {};
                            tmp.id = anotherArray[j]['com.fsi.creditcarddisputecase.AdditionalInformation'].questionId;
                            tmp.question = anotherArray[j]['com.fsi.creditcarddisputecase.AdditionalInformation'].questionPrompt;
                            finalResult.push(tmp);
                        }
                    }
                }
            }
        }

        return finalResult;
    } else {
        console.log("No")
        return response;
    }

    return finalResult;
}
export const processJsonDynamic = (response) => {
    //console.log(response);
    if (typeof response == "object") {
        var finalResult = [];
        var length = response.result["execution-results"].results.length;
        var resultArray = response.result["execution-results"].results;
        for (var i = 0; i < length; i++) {
            if (resultArray[i].key === 'questions') {
                var additionalInfoArray = resultArray[i].value;
                for (var j = 0; j < additionalInfoArray.length; j++) {
                    var oneMoreArray = Object.keys(additionalInfoArray[j]);
                    for (var k = 0; k < oneMoreArray.length; k++) {
                        if (oneMoreArray[k] === 'com.fsi.creditcarddisputecase.AdditionalInformation') {
                            var tmp = {};
                            tmp.questionLevel = additionalInfoArray[j]['com.fsi.creditcarddisputecase.AdditionalInformation'].questionLevel;
                            tmp.question = additionalInfoArray[j]['com.fsi.creditcarddisputecase.AdditionalInformation'].questionPrompt;
                            tmp.type = additionalInfoArray[j]['com.fsi.creditcarddisputecase.AdditionalInformation'].questionType;
                            tmp.questionOrder = additionalInfoArray[j]['com.fsi.creditcarddisputecase.AdditionalInformation'].questionOrder
                            if(additionalInfoArray[j]['com.fsi.creditcarddisputecase.AdditionalInformation'].questionType == "list") {
                                tmp.options = additionalInfoArray[j]['com.fsi.creditcarddisputecase.AdditionalInformation'].listOptions;
                            }else{
                                tmp.options = "null";
                            }
                            finalResult.push(tmp);
                        }
                    }
                }
            }
        }

        return finalResult;
    } else {
        console.log("No")
        return response;
    }
}
export const createCasePayload = (userProfile) => {

    const totalFraudAmount = parseInt(userProfile.tfrdamnt);
    const customerStatus = userProfile.cStatus;
    const customerAge = parseInt(userProfile.age);
    const incidentCount = parseInt(userProfile.incCount);
    const name = userProfile.name;
    const lname = userProfile.lname;
    const lstatmentb = userProfile.lstatmentb;
    const avaCredit = userProfile.avaCredit;
    const minCredit = userProfile.minCredit;
    const crntBal = userProfile.crntBal;
    const tranxtn = userProfile.tranxtn;

    const payload =
    {
        "case-data": {
            "totalFraudAmount": totalFraudAmount,
            "customerStatus": customerStatus,
            "customerAge": customerAge,
            "incidentCount": incidentCount,
            name: name,
            lname: lname,
            lstatmentb: lstatmentb,
            avaCredit: avaCredit,
            minCredit: minCredit,
            crntBal: crntBal,
            tranxtn: tranxtn,
        },
        "case-user-assignments": {
            "owner": "kieAdmin",
            "fraud-manager": "kieAdmin"
        },
        "case-group-assignments": {
            "fraud-manager": "IT"
        }
    };

    return payload;
}



export const additionalQuestionInputBody = (questionLevel, age, stateCode, status, isMinor, answer) =>{
    return (
        {
            "lookup": null,
            "commands": [{
                "insert": {
                    "object": {
                        "com.fsi.creditcarddisputecase.Cardholder": {
                            "questionLevel": questionLevel,
                            "age": age,
                            "stateCode": stateCode,
                            "status": status,
                            "isMinor": isMinor,
                            "answer": answer
                        }
                    },

                    "out-identifier": "cardholder",
                    "return-object": true,
                    "entry-point": "DEFAULT",
                    "disconnected": false
                }
            }, {
                "set-focus": {
                    "name": "dynamic-questions"
                }
            }, {
                "fire-all-rules": {
                    "max": -1,
                    "out-identifier": "dynamic-questions-fired"
                }
            }, {
                "get-objects": {
                    "class-object-filter": null,
                    "out-identifier": "questions"
                }
            }, {
                "set-focus": {
                    "name": "cleanup-questions"
                }
            }, {
                "fire-all-rules": {
                    "max": -1,
                    "out-identifier": "cleanup-questions-fired"
                }
            }, {
                "set-focus": {
                    "name": "cleanup-cardholder"
                }
            }, {
                "fire-all-rules": {
                    "max": -1,
                    "out-identifier": "cleanup-cardholder-fired"
                }
            }]
        }
    )
}
export const dynamicSort = (property) => {
    var sortOrder = 1;
    if(property[0] === "-") {
        sortOrder = -1;
        property = property.substr(1);
    }
    return function (a,b) {
        var result = (a[property] < b[property]) ? -1 : (a[property] > b[property]) ? 1 : 0;
        return result * sortOrder;
    }
}



