export const getAdditionalQuestions = (accessToken, questionLevel, age, stateCode, status, isMinor, answer) => {
    var TARGET_INPUT;

    TARGET_INPUT = utility.additionalQuestionInputBody(questionLevel, age, stateCode, status, isMinor, answer)
    return dispatch => {
        dispatch(action.restCallStart());

        var payloadArray = [];
        request
            .post(constant.GET_DYNAMIC_QUESTIONS_702_TARGET_URL)
            .set('Content-Type', 'application/json')
            .set('Accept', 'application/json')
           .set('Authorization', 'Bearer ' + accessToken)
            // .auth('pamAdmin', 'Password@100')
            .send(TARGET_INPUT)
            .then((res) => {
                dispatch(action.restCallSuccess());
                var tmp = {};
                tmp.status = res.status;
                tmp.body = res.body;
                //console.log(JSON.stringify(tmp.body));
                var customerInfo = tmp.body.result["execution-results"].results;

                for (var i = 0; i < customerInfo.length; i++) {
                    if (customerInfo[i].key === 'questions') {
                        var additionalInfoArray = customerInfo[i].value;
                        console.log(additionalInfoArray);
                        for (var j = 0; j < additionalInfoArray.length; j++) {
                            var oneMoreArray = Object.keys(additionalInfoArray[j]);
                            for (var k = 0; k < oneMoreArray.length; k++) {
                                if (oneMoreArray[k] === 'com.fsi.creditcarddisputecase.Cardholder') {
                                    var isMinor = additionalInfoArray[j]['com.fsi.creditcarddisputecase.Cardholder'].isMinor;
                                    console.log("remoteAction: " + isMinor);
                                        dispatch({type: actionTypes.SET_IS_MINOR, payload: isMinor});
                                }
                            }
                        }
                    }
                }
                //   payloadArray = utility.processJson(constant.MOCK_DYNAMIC_QUESTION);
                payloadArray = utility.processJsonDynamic(tmp.body);
                //console.log(payloadArray);
                payloadArray.sort(utility.dynamicSort("questionOrder"));
                return dispatch({ type: actionTypes.GET_ADDITIONAL_QUESTIONS, payload: payloadArray });
            })
            .catch((err) => {
                var tmp = {};
                console.log(err);
                tmp.errorMessage = err.message;
                tmp.errorResponse = err.response;
                dispatch(action.restCallFail(tmp.errorResponse));
            });
    }
}

