import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import * as actions from '../../state/actions/index';

// var userProfile = {
//     age: 25,
//     stateCode: "NY",
//     status: "PLATINUM",
//     isMinor: true,
//     answer: false
// }

var initialOrNot = true;

const styles = theme => ({
    progress: {
        margin: theme.spacing.unit * 2,
    },
})

const DynamicQuestions = props => {

    const propsQuestions = props.questions;
    var myQuestions = [];
    var myQuestionLevel = [];
    var myQuestionTypes = [];
    var myQuestionOptions = [];
    var myQuestionOrder = [];

    const questions = propsQuestions.map((question) => {
        //   console.log(JSON.stringify(question));
        myQuestions.push(question.question);
        myQuestionTypes.push(question.type);
        myQuestionOptions.push(question.options);
        myQuestionLevel.push(question.questionLevel);
        myQuestionOrder.push(question.questionOrder);
    });

    function handleAnswer(e, question, level, order) {
        var tmp = {};

        tmp.question = question
        tmp.answer = e.target.value;
        tmp.level = level;
        tmp.order = order;
        var stateVar = [];

        stateVar = props.additionalQuestionAnswers;
        if (stateVar.length === 0) {
            stateVar.push(tmp);
            //console.log("empty, pushing into state");
        } else {

            for (var i = 0; i < stateVar.length; i++) {
                if (stateVar[i].question === tmp.question) {
                    // answer already exists, replace existing.
                    stateVar[i].answer = tmp.answer;
                } else {
                    if (i === (stateVar.length - 1)) {
                        // question does not exist in state, push it in.
                        stateVar.push(tmp);
                    }
                }
            }

        }
        props.setAdditionalQuestionsAnswer(stateVar);
        //console.log('This is' + JSON.stringify(tmp));

    }


    const print = (index) => {
        var typeDisplay;
        // var myAnswer;
        //console.log("question length :" + myQuestions.length);

        if (index < 0) {
            index = 0;
            props.jumpToStep(2);
        } else if (index > myQuestions.length - 1 && index !== 0) {
            // when all the questions are done
            if (myQuestions[index - 1] == undefined) {
                props.jumpToStep(4);
            } else {
                props.loadNextLevelQuestions(myQuestionLevel[0] + 1);
                if (myQuestions.length == 0) {
                    props.jumpToStep(4);
                } else {
                    initialOrNot = false;
                    props.resetCounter();
                }
            }
        }
        //console.log("initial is: " + initialOrNot);
        if (initialOrNot == false && myQuestions.length == 0) {
            props.jumpToStep(4);
        }

        if (myQuestions.length > 0) {
            if (myQuestionTypes[index] === "boolean") {
                typeDisplay = (
                    <div><label className="radio-inline"><input type="radio" value="true" name="optradio" onChange={e => handleAnswer(e, myQuestions[index], myQuestionLevel[index], myQuestionOrder[index])} />Yes</label>
                        <label className="radio-inline"><input type="radio" value="false" name="optradio" onChange={e => handleAnswer(e, myQuestions[index], myQuestionLevel[index], myQuestionOrder[index])} />No</label>
                    </div>
                );
            } else if (myQuestionTypes[index] === "list") {
                var optionArray = myQuestionOptions[index].split(",");
                var result = [];

                for (var i = 0; i < optionArray.length; i++) {
                    result.push(
                        <option value={optionArray[i]} key={optionArray[i]}>{optionArray[i]}</option>
                    )
                }
                typeDisplay = (
                    <div>
                        <select
                            defaultValue=""
                            onChange={e => handleAnswer(e, myQuestions[index], myQuestionLevel[index], myQuestionOrder[index])}
                        >
                            {/* {console.log(props.additionalQuestionAnswers[index])} */}
                            <option value="" disabled hidden>
                                Choose here
                    </option>
                            {result}
                        </select>
                    </div>
                )
            } else {
                //   console.log(myQuestionTypes[index]);
                if (myQuestions[index] != null) {
                    typeDisplay = (
                        <div>
                            <input type="text" name="textbox" onChange={e => handleAnswer(e, myQuestions[index], myQuestionLevel[index], myQuestionOrder[index])} />
                        </div>
                    )
                }
            }
        }

        return (
            <div className="row" key={myQuestions[index]} >
                <div className="DecisionQuestionApp__spacer">
                    <div className="col-lg-3">
                        <p> {myQuestions[index]} </p>
                    </div>
                    <div className="col-lg-4">
                        {/* <input type="text" name="answer" onChange={(e) => props.handleAnswer(myQuestions[index], e)} value={currentValues[myQuestions[index]]} /> */}
                        {typeDisplay}

                    </div>
                </div>
                <br />
            </div>
        );

    }

    // function goNext() {
    //     props.increment();
    // }
    // function goPrev() {
    //     props.decrement();
    // }


    return (
        <div>
            <div className="row">
                <div className="col-lg-12">
                    {/* {questions} */}
                    {print(props.counter)}
                </div>
            </div>
            <div className="row">
                <button name="prev" type="button" className="btn btn-primary  pull-left stepZillLeftabt" onClick={() => props.decrement()} ><i className="fa fa-chevron-left"></i></button>
                <button name="next" type="button" className="btn btn-primary  pull-right stepZillRightabt" onClick={() => props.increment()} >Next</button>
            </div>

        </div>
    );
}

class DynamicQuestionnaire extends Component {
    state = {
        counter: 0,
        tempBool: false,
        isMinor: ""
    }
    componentDidMount() {
        const accessToken = this.props.accessToken;
      //  const userProfile = this.props.userProfile;
        this.props.getAdditionalQuestions(accessToken, 1, this.props.userProfile.age, this.props.userProfile.stateCode, this.props.userProfile.cStatus, null, null);
    }

    handleAnswer = (questionId, e) => {
        const target = e.target;
        const value = target.value;

        var currentAnswer = this.props.additionalQuestionAnswers;
        currentAnswer[questionId] = value;
        this.props.setAdditionalQuestionsAnswer(currentAnswer);
    }

    increment = () => {
        var jsonObj = this.state;
        jsonObj.counter = jsonObj.counter + 1;
        jsonObj.isMinor = this.props.isMinor;
        this.setState({
            jsonObj
        });
    }

    decrement = () => {
        var jsonObj = this.state;
        jsonObj.counter = jsonObj.counter - 1;
        this.setState({
            jsonObj
        });
    }

    resetCounter = () => {
        var jsonObj = this.state;
        jsonObj.counter = 0;
        this.setState({
            jsonObj
        });
    }

    loadNextLevelQuestions = (level) => {

        const accessToken = this.props.accessToken;
        const additionalQuestionAnswers = this.props.additionalQuestionAnswers;
        
        const userAnswer = additionalQuestionAnswers[additionalQuestionAnswers.length - 1].answer;
        if(this.state.tempBool == false) {
            this.props.getAdditionalQuestions(accessToken, level, this.props.userProfile.age, this.props.userProfile.stateCode, this.props.userProfile.cStatus, this.props.isMinor, userAnswer);
            this.setState({tempBool:true});
        }else{
            console.log("isMinor is " + this.state.isMinor);
                this.props.getAdditionalQuestions(accessToken, level, this.props.userProfile.age, this.props.userProfile.stateCode, this.props.userProfile.cStatus, this.props.isMinor, userAnswer)
        }
    }
    render() {
        const { classes } = this.props;
        let question_content =
            <DynamicQuestions
                jumpToStep={this.props.jumpToStep}
                increment={this.increment}
                decrement={this.decrement}
                resetCounter={this.resetCounter}
                counter={this.state.counter}
                questions={this.props.additionalQuestions}
                handleAnswer={(questionId, e) => this.handleAnswer(questionId, e)}
                setAdditionalQuestionsAnswer={this.props.setAdditionalQuestionsAnswer}
                additionalQuestionAnswers={this.props.additionalQuestionAnswers}
                loadNextLevelQuestions={this.loadNextLevelQuestions}
            />;

        if (this.props.loading) {
            question_content =
                <CircularProgress className={classes.progress} size={50} />;
        }
        if (this.props.error) {
            question_content =
                <div>
                    <p>At the moment, the system cannot connect to the server, please return later. Sorry for the inconvenience.</p>
                    <p>Error:  {this.props.error}</p>
                </div>;
        }

        return (
            <div>
                <br />
                <div className="row">
                    <p className="DecisionQuestionApp__question">Let us ask a few questions</p>
                    <div className="col-lg-12">
                        <label className="DecisionQuestionApp__step"><b> Additional Questions: </b></label>
                    </div>
                    <br />
                    <br />
                </div>
                {question_content}
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        additionalQuestions: state.transactionDisputeReducer.additionalQuestions,
        additionalQuestionAnswers: state.transactionDisputeReducer.additionalQuestionAnswers,
        loading: state.transactionDisputeReducer.loading,
        error: state.transactionDisputeReducer.error,
        accessToken: state.transactionDisputeReducer.accessToken,
        userProfile: state.transactionDisputeReducer.userProfile,
        isMinor: state.transactionDisputeReducer.isMinor,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        getAdditionalQuestions: (accessToken, questionLevel, age, stateCode, status, isMinor, answer) => dispatch(actions.getAdditionalQuestions(accessToken, questionLevel, age, stateCode, status, isMinor, answer)),
        setAdditionalQuestionsAnswer: (payload) => dispatch(actions.setAdditionalQuestionsAnswer(payload)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(DynamicQuestionnaire));


